import React, { useEffect, useState } from "react";
import { IconButton, ListItem, List } from "@mui/material";
import HelpIcon from "@mui/icons-material/Help";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Button from "@mui/material/Button";

function Helper() {
  const [dialogOpen, setDialogOpen] = useState(false);

  const help = () => {
    setDialogOpen(true);
  };

  const handleCloseHelperDialog = () => {
    setDialogOpen(false);
  };

  useEffect(() => {}, [dialogOpen]);

  return (
    <>
      <IconButton onClick={help} sx={{ marginLeft: 1, color: "#b6017f" }}>
        <HelpIcon />
      </IconButton>
      <Dialog open={dialogOpen} onClose={handleCloseHelperDialog}>
        <DialogTitle id="alert-dialog-title" sx={{ color: "#ff1bb8" }}>
          Hur gr det till?
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <List sx={{ listStyleType: "disc", color: "#fe4ec8" }}>
              <ListItem sx={{ display: "list-item" }}>
                Ta dig till 6 destinationer med hjlp av ledtrdar.
              </ListItem>
              <ListItem sx={{ display: "list-item" }}>
                Det finns 2 niver till varje ledtrd, frst en text och sen en
                bild.
              </ListItem>
              <ListItem sx={{ display: "list-item" }}>
                Vid varje plats fr man ledtrdar som man behver fr att hitta
                skatten. Du kan hitta ledtrdarna genom att trycka p "HITTADE
                LEDTRDAR" knappen.
              </ListItem>
              <ListItem sx={{ display: "list-item" }}>
                Nr man har varit vid alla 6 destinationer s fr man en ledtrd
                till den sista destinationen dr skatten finns!
              </ListItem>
            </List>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseHelperDialog} sx={{ color: "#ff1bb8" }}>
            Jag frstr!
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

export default Helper;
