import { Box, Button } from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import React, { useEffect, useState } from "react";
import IMG from "../../image/image";
import { buttonTheme, textTheme } from "../../style/theme";

function FinalClue() {
  const [dialogOpen, setDialogOpen] = useState(false);
  const [allCluesFound, setAllCluesFound] = useState(false);
  const [firstHint, setFirstHint] = useState(true);

  const showFoundClues = () => {
    setDialogOpen(true);
  };

  const closeClueDialog = () => {
    setDialogOpen(false);
  };

  const nextHint = () => {
    if (firstHint) {
      setFirstHint(false);
    } else {
      setFirstHint(true);
    }
  };

  const getImage = () => {
    const locationObject = JSON.parse(localStorage.getItem("locationsList"));
    const location = Object.values(locationObject)[0];
    var img = new IMG();

    if (location) {
      return img[location.image];
    }
  };

  const getFoundClues = () => {
    const rewardObject = JSON.parse(localStorage.getItem("reward"));
    var arr = [
      "_",
      "_",
      "_",
      " ",
      "_",
      "_",
      "_",
      " ",
      "_",
      "_",
      " ",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
    ];
    if (rewardObject) {
      rewardObject.forEach((reward) => {
        arr.splice(reward.index, 1, reward.letter);
      });

      return arr;
    }
    return "Hitta din frsta destination fr att f ledtrdar!";
  };

  useEffect(() => {
    const locationObject = JSON.parse(localStorage.getItem("locationsList"));
    const location = Object.values(locationObject)[0];

    if (location.locationName === "Home") {
      setAllCluesFound(true);
    }
  });

  return (
    <Box textAlign="center" style={{ marginTop: "150px" }}>
      <ThemeProvider theme={buttonTheme}>
        <Button variant="contained" onClick={showFoundClues}>
          Hittade ledtrdar
        </Button>
      </ThemeProvider>

      {dialogOpen && (
        <Dialog open={dialogOpen} onClose={closeClueDialog}>
          <DialogTitle id="alert-dialog-title" sx={{ color: "#ff1bb8" }}>
            Ledtrdar till det sista pusslet!
          </DialogTitle>
          <DialogContent>
            {firstHint && (
              <ThemeProvider theme={textTheme}>
                <DialogContentText sx={{ color: "#fe4ec8" }}>
                  {getFoundClues()}
                </DialogContentText>
              </ThemeProvider>
            )}
            {!firstHint && (
              <Box
                component="img"
                src={getImage()}
                sx={{ height: "auto", width: "100%" }}
              />
            )}
          </DialogContent>
          <DialogActions>
            <ThemeProvider theme={buttonTheme}>
              {allCluesFound && (
                <Button onClick={nextHint} variant="contained">
                  Nsta ledtrd!
                </Button>
              )}
              <Button onClick={closeClueDialog}>Stng</Button>
            </ThemeProvider>
          </DialogActions>
        </Dialog>
      )}
    </Box>
  );
}

export default FinalClue;
