import { distanceToLocation } from "./distance";

export default function inRangeCheck(userLatitude, userLongitude) {
  const locationObject = JSON.parse(localStorage.getItem("locationsList"));
  const location = Object.values(locationObject)[0];

  if (location) {
    const locationCoords = { lat: location.lat, lon: location.long };
    const userPosition = { lat: userLatitude, lon: userLongitude };

    let distance = distanceToLocation(userPosition, locationCoords);
    // console.log(location, distance);
    // console.log(localStorage.getItem("reward"));
    // console.log(location.activationDistance);
    if (Math.floor(distance) < location.activationDistance) {
      if (localStorage.getItem("reward")) {
        var data = JSON.parse(localStorage.getItem("reward"));
        location.reward.forEach((reward) => {
          data.push(reward);
        });
        localStorage.setItem("reward", JSON.stringify(data));
      } else {
        localStorage.setItem("reward", JSON.stringify(location.reward));
      }
      delete locationObject[Object.keys(locationObject)[0]];
      localStorage.setItem("locationsList", JSON.stringify(locationObject));
      return true;
    }
  }
}
