import { Box, Button, ThemeProvider, Typography } from "@mui/material";
import { Container } from "@mui/system";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import React, { useEffect, useState } from "react";
import inRangeCheck from "../../utils/rangeCheck";
import IMG from "../../image/image";
import { buttonTheme, textTheme, titleTheme } from "../../style/theme";

function ProximityAlert(props) {
  const [inRange, setInRange] = useState(false);
  const [userLongitude, setUserLongitude] = useState();
  const [userLatitude, setUserLatitude] = useState();
  const [dialogOpen, setDialogOpen] = useState(false);
  const [firstHint, setFirstHint] = useState(true);
  const [hideHintButton, setHideHintButton] = useState(false);

  const onClickHintButton = () => {
    setDialogOpen(true);
    getImage();
  };

  const handleCloseHintDialog = () => {
    setDialogOpen(false);
    setFirstHint(true);
  };

  const handleCloseDestinationDialog = () => {
    setInRange(false);
  };

  const getImage = () => {
    const locationObject = JSON.parse(localStorage.getItem("locationsList"));
    const location = Object.values(locationObject)[0];
    var img = new IMG();

    if (location) {
      return img[location.image];
    }
  };

  const nextHint = () => {
    if (firstHint) {
      setFirstHint(false);
    } else {
      setFirstHint(true);
    }
  };

  const getHintText = () => {
    const locationObject = JSON.parse(localStorage.getItem("locationsList"));
    const location = Object.values(locationObject)[0];

    if (location && location.locationName !== "Home") {
      return location.clue;
    }
    return "Du behver bara hitta skatten!";
  };

  const getFoundClues = () => {
    const rewardObject = JSON.parse(localStorage.getItem("reward"));
    var arr = [
      "_",
      "_",
      "_",
      " ",
      "_",
      "_",
      "_",
      " ",
      "_",
      "_",
      " ",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
      "_",
    ];
    if (rewardObject) {
      rewardObject.forEach((reward) => {
        arr.splice(reward.index, 1, reward.letter);
      });

      return arr;
    }
  };

  useEffect(() => {
    setUserLongitude(props.location.longitude);
    setUserLatitude(props.location.latitude);
    function updateLocation() {
      const locationObject = JSON.parse(localStorage.getItem("locationsList"));
      const location = Object.values(locationObject)[0];

      if (location.locationName === "Home") {
        setHideHintButton(true);
      } else {
        setHideHintButton(false);
      }
      if (inRangeCheck(userLatitude, userLongitude)) {
        setInRange(true);
        setDialogOpen(false);
      }
    }
    updateLocation();
  }, [props.location]);

  return (
    <Container>
      <Box textAlign="center" style={{ marginTop: "60px" }}>
        {!hideHintButton && (
          <ThemeProvider theme={buttonTheme}>
            <Button variant="contained" onClick={onClickHintButton}>
              Ge mig en ledtrd!
            </Button>
          </ThemeProvider>
        )}
        {hideHintButton && (
          <>
            <Typography variant="body2" sx={{ color: "#fe4ec8" }}>
              Du har nu hittat alla ledtrdar!
            </Typography>
            <Typography variant="body2" sx={{ color: "#fe4ec8" }}>
              Anvnd{" "}
              <Box component="span" fontWeight="fontWeightBold">
                "HITTADE LEDTRDAR"
              </Box>{" "}
              knappen fr att se var sista destinationen r!
            </Typography>
          </>
        )}
      </Box>
      <Dialog open={dialogOpen} onClose={handleCloseHintDialog}>
        <ThemeProvider theme={titleTheme}>
          <DialogTitle id="alert-dialog-title" sx={{ color: "#ff1bb8" }}>
            Ledtrd
          </DialogTitle>
        </ThemeProvider>

        <DialogContent>
          {firstHint && (
            <ThemeProvider theme={textTheme}>
              <DialogContentText sx={{ color: "#fe4ec8" }}>
                {getHintText()}
              </DialogContentText>
            </ThemeProvider>
          )}
          {!firstHint && (
            <Box
              component="img"
              src={getImage()}
              sx={{ height: "auto", width: "100%" }}
            />
          )}
        </DialogContent>
        <DialogActions>
          <ThemeProvider theme={buttonTheme}>
            <Button onClick={nextHint} variant="contained">
              Nsta ledtrd!
            </Button>
            <Button onClick={handleCloseHintDialog} variant="outlined">
              Stng
            </Button>
          </ThemeProvider>
        </DialogActions>
      </Dialog>

      {inRange && (
        <Dialog open={inRange} onClose={handleCloseDestinationDialog}>
          <ThemeProvider theme={titleTheme}>
            <DialogTitle id="alert-dialog-title" sx={{ color: "#ff1bb8" }}>
              Du hittade en ledtrd!
            </DialogTitle>
          </ThemeProvider>
          <DialogContent>
            <ThemeProvider theme={textTheme}>
              <DialogContentText sx={{ color: "#fe4ec8" }}>
                Hr r en del av ledtrden som du kan anvnda fr att lsa det
                sista pusslet!
              </DialogContentText>
              <DialogContentText sx={{ color: "#fe4ec8" }}>
                {getFoundClues()}
              </DialogContentText>
            </ThemeProvider>
          </DialogContent>
          <DialogActions>
            <ThemeProvider theme={buttonTheme}>
              <Button
                onClick={handleCloseDestinationDialog}
                sx={{ color: "#fe4ec8" }}
              >
                Stng
              </Button>
            </ThemeProvider>
          </DialogActions>
        </Dialog>
      )}
    </Container>
  );
}

export default ProximityAlert;
