import { Typography } from "@mui/material";
import { Container } from "@mui/system";
import { Box, Button } from "@mui/material";
import React, { useEffect } from "react";
import ProximityAlert from "./components/alert/ProximityAlert";
import Location from "./components/location";
import { geolocationOptions } from "./constants/geolocationOptions";
import useWatchLocation from "./hooks/useWatchLocation";
import data from "./location.json";
import Helper from "./components/helper/Helper";
import FinalClue from "./components/finalClue/FinalClue";

function App() {
  const { location, error } = useWatchLocation(geolocationOptions);

  const onReset = () => {
    localStorage.setItem("locationsList", JSON.stringify(data));
  };

  useEffect(() => {
    if (localStorage.getItem("locationsList")) {
      console.log("I found existing localStorage");
    } else {
      localStorage.setItem("locationsList", JSON.stringify(data));
    }
  }, []);

  return (
    <Container>
      <Box textAlign="center" sx={{ display: "flex", alignItems: "center" }}>
        <Typography
          align="center"
          variant="h3"
          sx={{ marginLeft: 8, color: "#ff1bb8" }}
        >
          Skattjakt!
        </Typography>

        <Helper />
      </Box>
      {/* <Box textAlign="center" style={{ marginTop: "15px" }}>
        <Button variant="contained" onClick={onReset}>
          Reset!
        </Button>
      </Box> */}
      {location !== undefined && <ProximityAlert location={location} />}
      {/* <Location location={location} error={error} /> */}
      <FinalClue />
    </Container>
  );
}

export default App;
